.myButton {
  width: 100%;
  height: 40px;
  padding: 0rem 1rem;
  outline: none;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  background: rgb(218, 21, 7);
  color: #fff;
  cursor: pointer;
}
.myButton:active {
  background: rgb(0, 88, 37);
  transition: linear 0.2s all;
}
